import React, { memo } from 'react';
import { compose } from 'recompose';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { Card, CardMedia, CardContent, CardActionArea, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

import styles from './styles';

const ServiceOnDemandCard = memo(({
  classes, service, history
}) => {
  const onClick = () => history.push(`/servicos/${service.id}/solicitar-servico`);

  return (
    <Card onClick={onClick}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image={service.image_url}
          title={service.title}
        />
        <CardContent className={classes.content}>
          <Typography variant="h6">
            <small>
              <b>{service.title}</b>
            </small>
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
});

ServiceOnDemandCard.propTypes = {
  classes: PropTypes.object,
  service: PropTypes.object,
  history: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter
)(ServiceOnDemandCard);
