import React, { memo } from 'react';
import { map, get } from 'lodash';
import { compose } from 'recompose';
import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';

import Loading from '../../../components/Loading';
import SERVICE_TYPES from '../service-types';
import ServiceService from '../../../services/service';
import withRequest from '../../../utils/with-request';
import ServiceSubscriptionCard from './ServiceSubscriptionCard';
import CustomRequestCard from './CustomRequestCard';

const Subscription = memo(({
  servicesData, servicesLoading: loading
}) => {
  if (loading) return <Loading />;
  return (
    <React.Fragment>
      <Grid container spacing={2}>
        {
          map(get(servicesData, 'services', []), service => (
            <Grid key={service.id} item xs={12} sm={4}>
              <ServiceSubscriptionCard
                service={service}
              />
            </Grid>
          ))
        }
        <Grid item xs={12} sm={4}>
          <CustomRequestCard />
        </Grid>
      </Grid>
    </React.Fragment>
  );
});

Subscription.propTypes = {
  servicesData: PropTypes.object,
  servicesLoading: PropTypes.bool
};

const withServices = withRequest(ServiceService.getServices, {
  name: 'services',
  options: ({ setAlert }) => ({
    variables: { serviceType: SERVICE_TYPES.SUBSCRIPTION },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withServices
)(Subscription);
