import React, { memo } from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import { get as _get, size as _size } from 'lodash';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import withRequest from '../../utils/with-request';
import ServiceRequestsService from '../../services/service-request';
import MyRequestsTable from './MyRequestsTable';
import ServiceRequestEnums from '../../enums/service-request';
import FormSelectField from '../../components/FormFields/FormSelectField';
import Loading from '../../components/Loading';
import styles from './styles';

const { SERVICE_REQUEST_STATUSES } = ServiceRequestEnums;

const MyRequests = memo(({
  alert, setAlert, classes, serviceRequestsData, serviceRequestsLoading,
  status, setStatus, serviceRequestsFetch
}) => {
  let serviceRequests = _get(serviceRequestsData, 'service_requests');
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const onSelectStatusFilter = (e) => {
    setStatus(e.target.value);
    serviceRequestsFetch(true);
    serviceRequests = _get(serviceRequestsData, 'service_requests');
  };
  const countServiceRequests = !serviceRequestsLoading ? `(${_size(serviceRequests)})` : '';
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <Grid container>
              <Grid item xs={12} md={12}>
                <Typography variant="h5">
                  <b>{`Meus pedidos ${countServiceRequests}`}</b>
                </Typography>
              </Grid>
              <Grid item xs={6} md={2}>
                <FormSelectField
                  label="Status"
                  value={status}
                  disabled={serviceRequestsLoading}
                  onChange={(e) => onSelectStatusFilter(e)}
                  options={SERVICE_REQUEST_STATUSES}
                  emptyOptionLabel="Todos"
                  variant="standard"
                />
              </Grid>
            </Grid>
          </div>
        </div>
        <div className={classes.container}>
          {serviceRequestsLoading ?
            <Loading /> :
            <MyRequestsTable
              filteredStatus={status}
              serviceRequests={serviceRequests}
              serviceRequestsLoading={serviceRequestsLoading}
            />}
        </div>
      </section>
    </div>
  );
});

MyRequests.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  serviceRequestsData: PropTypes.object,
  serviceRequestsLoading: PropTypes.bool,
  serviceRequestsFetch: PropTypes.func,
  status: PropTypes.string,
  setStatus: PropTypes.func
};

const withServiceRequests = withRequest(ServiceRequestsService.getServiceRequests, {
  name: 'serviceRequests',
  options: ({ setAlert, status = null }) => ({
    variables: { status },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('status', 'setStatus', ''),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.role)
  }),
  withServiceRequests
)(MyRequests);
