import React from 'react';
import { camelCase, upperFirst } from 'lodash';
import PropTypes from 'prop-types';

import {
  TEXT_FIELD, TEXT_AREA, SELECT, CHECKBOX, ATTACHMENT
} from '../../enums/fields';
import FormTextField from './FormTextField';
import FormSelectField from './FormSelectField';
import FormCheckboxListField from './FormCheckboxListField';
import FormAttachmentField from './FormAttachmentField';

const ALL_FIELD_TYPES = [
  TEXT_FIELD, TEXT_AREA, SELECT, CHECKBOX, ATTACHMENT
];

const fieldBuilders = {
  buildTextField({
    ...otherProps
  }) {
    return (
      <FormTextField
        {...otherProps}
      />
    );
  },
  buildTextArea({
    ...otherProps
  }) {
    return (
      <FormTextField
        multiline
        rows={4}
        rowsMax={4}
        {...otherProps}
      />
    );
  },
  buildSelect({
    options,
    ...otherProps
  }) {
    return (
      <FormSelectField
        options={options}
        {...otherProps}
      />
    );
  },
  buildCheckbox({
    ...otherProps
  }) {
    return (
      <FormCheckboxListField
        {...otherProps}
      />
    );
  },
  buildAttachment({
    ...otherProps
  }) {
    return (
      <FormAttachmentField
        {...otherProps}
      />
    );
  }
};
fieldBuilders.buildSelect.propTypes = {
  options: PropTypes.array
};

const FormDynamicField = ({
  fieldType: fieldTypeProp,
  ...otherProps
}) => {
  if (!ALL_FIELD_TYPES.includes(fieldTypeProp)) {
    throw new Error(`Invalid field type: ${fieldTypeProp}`);
  }

  const fieldType = upperFirst(camelCase(fieldTypeProp));
  return fieldBuilders[`build${fieldType}`](otherProps);
};

FormDynamicField.propTypes = {
  fieldType: PropTypes.oneOf(ALL_FIELD_TYPES)
};

export default FormDynamicField;
