import React, { memo } from 'react';
import PropTypes from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import _findIndex from 'lodash/findIndex';
import { withRouter } from 'react-router';

const tabMenu = [
  { label: 'sob-demanda', path: 'sobdemanda' },
  { label: 'recorrentes', path: 'recorrentes' }
];

const ServicesMenu = memo(({ pathBase, history }) => (
  <Paper square>
    <Tabs
      value={_findIndex(tabMenu, t => history.location.pathname === `${pathBase}/${t.path}`)}
      indicatorColor="primary"
      textColor="primary"
      scrollButtons="auto"
      variant="scrollable"
      onChange={(e, index) => history.push(`${pathBase}/${tabMenu[index].path}`)}
    >
      {tabMenu.map((t, index) => <Tab key={t.path} label={t.label} index={index} />)}
    </Tabs>
  </Paper>
));

ServicesMenu.propTypes = {
  pathBase: PropTypes.string,
  history: PropTypes.object
};

export default withRouter(ServicesMenu);
